import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { ProductSummaryItem } from '@backbase/data-ang/arrangements';

@Component({
  selector: 'bb-beneficiary-account',
  templateUrl: 'beneficiary-account.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BeneficiaryAccountComponent {
  /**
   * Own account item
   */
  @Input()
  set account(item: ProductSummaryItem) {
    this._account = item;
    if (item) {
      this.accountNumber = item.BBAN || item.number || '';
      this.balance = item.availableBalance || item.bookedBalance || 0;
    }
  }
  get account() {
    return this._account;
  }
  private _account!: ProductSummaryItem;

  accountNumber?: string;
  balance?: number;
}
